$dot_color: #8bbe56;
.vis_content {
  max-width: 90vw;

  .vis_text {
    max-width: 900px;
    margin: 0 auto;
    font-size: 21px;
    text-align: center;
    margin-top: 20px;
    .total_study_highlight {
      padding: 0 4px;
      background-color: $dot_color;
      font-weight: 700;
    }
    .dot_color {
      color: $dot_color;
      font-weight: 700;
    }
    .criteria_category {
      background-color: #cecedd;
      padding: 0 4px;
    }
  }
}
